
.header {
  position: relative;
  width: 100%;
  height: 70px;
  z-index: 100;
}
.header h1 {
    position: relative;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    line-height: 70px;
    text-align: center;
    color: #fff;
}
.header h1 a.t-btn {
      position: absolute;
      right: 10px;
      top: 22px;
      width: 30px;
      height: 26px;
      cursor: pointer;
}
.header h1 span, .header h1 span:before, .header h1 span:after {
      position: absolute;
      left: 0;
      width: 30px;
      height: 4px;
      content: '';
      background: #fff;
}
.header h1 span {
      top: 11px;
}
.header h1 span:before {
        top: 0;
        -webkit-transform: translateY(-7px);
                transform: translateY(-7px);
        transition: all .3s;
}
.header h1 span:after {
        -webkit-transform: translateY(7px);
                transform: translateY(7px);
        transition: all .3s;
}



